<template>
  <va-card
    :color="computedWidgetColor"
    :style="computedStyle"
    :title="$t('dashboard.profile.title')"
  >
    <va-button
      @click="getMyProfile"
      class="mr-0"
      small
      outline
      slot="actions"
      color="info"
    >
      {{ $t('dashboard.profile.myProfile') }}
    </va-button>
    <div class="profile__name">
      <va-icon name="fa fa-user-circle" class="ml-2 mr-2"></va-icon>
      <div class="profile__name_first text--capitalize">
        {{ user.firstName || 'Loading...' }}
      </div>
      <div class="profile__name_last text--bold text--capitalize">
        {{ user.lastName }}
      </div>
    </div>
    <va-tabs grow v-model="tabsState" :style="computedTabsStyle">
      <va-tab v-for="(tab, i) in tabs" :key="i">
        {{
          $t(
            'dashboard.profile.tabs.' +
              tab.toLowerCase().replace('profile', '') +
              '.title'
          )
        }}
      </va-tab>
    </va-tabs>
    <component
      :loading="loadingTabs.includes(tabs[tabsState])"
      :class="['', '', 'pb-4'][this.tabsState]"
      :is="tabs[tabsState]"
    />
  </va-card>
</template>

<script>
import ProfileInfo from './dashboard-profile-tabs/ProfileInfo'
import ProfileWallets from './dashboard-profile-tabs/ProfileWallets'
import ProfileLog from './dashboard-profile-tabs/ProfileLog'
import ProfileActions from './dashboard-profile-tabs/ProfileActions'
import ProfileBind from './dashboard-profile-tabs/ProfileBind'
import { Me, UserById } from '../../services/Users'

export default {
  name: 'DashboardProfile',
  components: {
    ProfileInfo,
    ProfileWallets,
    ProfileLog,
    ProfileActions,
    ProfileBind,
  },
  data: () => ({
    user: {
      id: '...',
      role: '...',
      username: '...',
      transfers: '...',
      telegramUsername: '...',
      name: '',
      email: '...',
      bindedTo: '...',
      actions: [],
      log: [],
      me: false,
      wallets: 'empty',
    },
    loadingTabs: [
      'ProfileInfo',
      'ProfileWallets',
      'ProfileLog',
      'ProfileActions',
    ],
    tabsState: 0,
    myRole: '',
  }),
  provide() {
    return {
      user: this.user,
    }
  },
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedTabsStyle() {
      return {
        color: this.$themes.secondary,
      }
    },
    tabs() {
      let ts = ['ProfileInfo']

      if (this.user.role === 'user') ts.push('ProfileWallets')
      ts.push('ProfileLog')
      ts.push('ProfileActions')
      if (this.myRole === 'manager') ts.push('ProfileBind')

      this.tabsState =
        this.tabsState > ts.length - 1 ? ts.length - 1 : this.tabsState

      return ts
    },
  },
  methods: {
    getMyProfile() {
      this.loading()
      this.$root.$emit('getting-dialogue')

      Me()
        .then(({ data: user }) => {
          this.myRole = user.role
          this.$root.$emit('open-dialogue', user)
          this.updateOpenedProfile(user, true)
        })
        .catch(err => {
          this.loaded()
          this.showToast(
            'Cannot open your profile. Status code ' +
              (err.response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'error',
            }
          )
        })
    },
    updateOpenedProfile(user, me) {
      this.$root.$emit('update-wallet', user)
      
      this.loaded()
      this.user.me = !!me
      ;[this.user.firstName, this.user.lastName] = user.name.split(' ')

      if (Object.keys(user).length) {
        Object.keys(user).forEach(key => {
          this.user[key] = user[key]
        })
      }
    },
    refreshOpenedProfile() {
      this.$root.$emit('getting-dialogue')

      UserById(this.user.id)
        .then(({ data }) => {
          this.$root.$emit('open-dialogue', this.user)
          this.$root.$emit('open-profile', data)
        })
        .catch(err => {
          this.getMyProfile()
          this.showToast(
            'Cannot open his profile. Status code ' +
              (err.response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'error',
            }
          )
        })
    },
    loading() {
      this.loadingTabs = [
        'ProfileInfo',
        'ProfileWallets',
        'ProfileLog',
        'ProfileActions',
      ]
    },
    loaded() {
      this.loadingTabs = []
    },
    gettingProfile() {
      this.loading()
    },
  },
  created() {
    this.getMyProfile()
    this.$root.$on('getting-profile', this.gettingProfile)
    this.$root.$on('open-profile', this.updateOpenedProfile)
    this.$root.$on('refresh-profile', this.refreshOpenedProfile)
  },
  beforeDestroy() {
    this.$root.$off('getting-profile', this.loading)
    this.$root.$off('open-profile', this.updateOpenedProfile)
    this.$root.$off('refresh-profile', this.refreshOpenedProfile)
  },
}
</script>

<style lang="scss" scoped>
.profile__name {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  justify-content: center;
  align-items: center;
  display: flex;

  &_first,
  &_last {
    margin: 0 5px;
  }
}
</style>
