<template>
  <va-inner-loading :loading="loading || updating">
    <div class="tab" :style="computedStyle">
      <div class="tab__wallets pt-3 pb-2">
        <div class="row">
          <div class="flex xs12">
            <div
              v-for="(wallet, i) in wallets"
              :key="i"
              class="tab__item d-flex align--center"
            >
              <va-icon name="money" class="ml-3 mr-3"></va-icon>
              <div class="text--bold tab__item-name mr-5 text--capitalize">
                {{ wallet.currency }}
              </div>
              <div>
                {{ wallet.balance - wallet.deltaBalance }}
              </div>
              <div class="d-flex justify--end">
                <va-button-toggle
                  outline
                  small
                  v-model="deltaMode[i]"
                  :options="deltaModes"
                />
              </div>
              <va-input
                small
                class="ma-0"
                v-model="deltas[i]"
                :placeholder="
                  $t('dashboard.profile.tabs.wallets.deltaPlaceholder')
                "
              ></va-input>
            </div>
          </div>
          <div class="tab__item_save mt-2">
            <va-button @click="saveChanges">{{
              $t('dashboard.profile.tabs.wallets.save')
            }}</va-button>
          </div>
        </div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import axios from 'axios'

export default {
  name: 'profile-wallets-tab',
  inject: ['user'],
  props: ['loading'],
  data: () => ({
    updating: false,
    deltaMode: [1, 1, 1],
    deltas: [0, 0, 0],
  }),
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.updating || this.loading ? 0.2 : 1,
      }
    },
    deltaModes: () => [
      {
        value: 0,
        label: '-',
      },
      {
        value: 1,
        label: '+',
      },
    ],
    wallets() {
      return this.user.wallets.map((wallet, i) => ({ ...wallet, deltaMode: 1 }))
    },
  },
  methods: {
    updateWallet(user) {
      user.wallets.forEach((w, i) => {
        this.deltas[i] = w.deltaBalance
      })
    },
    saveChanges() {
      var wallets = {}

      this.wallets.forEach((w, i) => {
        if (w.deltaBalance != this.deltas[i]) {
          try {
            let newDeltaBalance = +this.deltas[i]

            if (newDeltaBalance != NaN) {
              wallets[w.currency] = newDeltaBalance
            } else {
              throw new Error()
            }
          } catch {
            this.showToast('The delta must be a number', {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'error',
            })
          }
        }
      })

      if (Object.keys(wallets).length) {
        this.updating = true
        axios
          .post(
            window.location.protocol +
              '//' +
              window.location.host +
              '/api/admin/update_delta',
            { user: this.user.id, wallets }
          )
          .then(data => {
            this.updating = false
            this.showToast('This wallet has been updated', {
              icon: 'fa-check',
              position: 'bottom-center',
              duration: 4000,
              type: 'success',
            })
          })
          .catch(error => {
            this.updating = false
          })
      }
    },
  },
  created() {
    this.updateWallet(this.user)
    this.$root.$on('update-wallet', this.updateWallet)
  },
  beforeDestroy() {
    this.$root.$off('update-wallet', this.updateWallet)
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.tab {
  &__item {
    height: 55px;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &_save {
      display: grid;
      width: 100%;
      justify-content: center;
    }

    &-name {
      min-width: 70px;
      max-width: 200px;
    }

    &:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }
}
</style>
